<template>
  <div id="passenger">
    <!--  -->
    <van-contact-card
      :type="cardType"
      :name="currentContact.name"
      :tel="currentContact.tel"
      add-text=""
      @click="showList = true"
    />

    <!--  -->
    <van-popup v-model="showList" position="bottom">
      <van-contact-list
        v-model="chosenContactId"
        add-text=""
        :list="list"
        @add="onAdd"
        @edit="onEdit"
        @select="onSelect"

      />
    </van-popup>

    <!--  -->
    <van-popup v-model="showEdit" position="bottom">
      <van-contact-edit
        :contact-info="editingContact"
        :is-edit="isEdit"
        @save="onSave"
        @delete="onDelete"
      />
    </van-popup>
  </div>
</template>

<script>
  import Vue from 'vue';
  import { ContactCard, ContactList, ContactEdit } from 'vant';
  import { Popup } from 'vant';

  Vue.use(Popup);
  Vue.use(ContactCard);
  Vue.use(ContactList);
  Vue.use(ContactEdit);

  export default {
    name: "Passenger",
    data() {
      return {
        chosenContactId: null,
        editingContact: {},
        showList: false,
        showEdit: false,
        isEdit: false,
        list: [{
          name: '',
          tel: '18260069008',
          id: 0
        },{
          name: '',
          tel: '13773329218',
          id: 1
        }]
      };
    },

    computed: {
      cardType() {
        return this.chosenContactId !== null ? 'edit' : 'add';
      },

      currentContact() {
        const id = this.chosenContactId;
        return id !== null ? this.list.filter(item => item.id === id)[0] : {};
      }
    },

    methods: {
      // 
      onAdd() {
        this.editingContact = { id: this.list.length };
        this.isEdit = false;
        this.showEdit = true;
      },

      // 
      onEdit(item) {
        this.isEdit = true;
        this.showEdit = true;
        this.editingContact = item;
      },

      // 
      onSelect() {
        this.showList = false;
      },

      // 
      onSave(info) {
        this.showEdit = false;
        this.showList = false;

        if (this.isEdit) {
          this.list = this.list.map(item => item.id === info.id ? info : item);
        } else {
          this.list.push(info);
        }
        this.chosenContactId = info.id;
      },

      // 
      onDelete(info) {
        this.showEdit = false;
        this.list = this.list.filter(item => item.id !== info.id);
        if (this.chosenContactId === info.id) {
          this.chosenContactId = null;
        }
      }
    }
  }
</script>

<style scoped>
 #passenger {
   margin: 5px;
   border-radius: 5px;
 }
</style>
